@import url(https://fonts.googleapis.com/css?family=Poppins:400,600);
body {
  background: gray;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif !important;

  background: #333 !important;
}
img {
  max-width: 100%;
}
.nasa-title {
  color: #fff;
  font-weight: 600;
}

.container {
  width: 1200px;
  margin: 20px auto;
  -webkit-columns: 4;
          columns: 4;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
}
.container .card {
  width: 100%;
  margin: 0 0 20px;
  padding: 10px;
  background: #fff;
  overflow: hidden;
  page-break-inside: void;
  -webkit-column-break-inside: void;
          break-inside: void;
}

.container .card .card-img-top {
  max-width: 100%;
  max-height: 300px;
}
.container .card .card-title {
  margin: 10px 0 0;
  padding: 0;
  font-size: 20px;
}
.container .card .card-subtitle {
  font-size: 12px;
}
.container .card .card-text {
  margin: 0;
  padding: 0 0 10px;
  font-size: 15px;
}
.card-body {
  padding: 0.25rem;
}
@media (max-width: 1200px) {
  .container {
    -webkit-columns: 3;
            columns: 3;
    width: calc(100%-40px);
    box-sizing: border-box;
    padding: 20px 20px 20px 0;
  }
}
@media (max-width: 760px) {
  .container {
    -webkit-columns: 2;
            columns: 2;
  }
}
@media (max-width: 480px) {
  .container {
    -webkit-columns: 1;
            columns: 1;
  }
}
.wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

